@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}

*{
  margin:0;
  padding: 0;
}

body{
  font-family: 'Raleway';
}

.App{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
/* .condiv class is for the right side content container*/
.condiv{
  padding: 100px ;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
}
.skills ul li{
  margin:10px;
  font-size: large;
}
.site-nav{
  position: relative;
  align-items: center;
}
.site-nav a{
  position: fixed;
  left: 80px;
  color: #C689B6;
  text-decoration: none;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.site-nav a:hover{
  border-radius: 25px;
  background-color: #EBA6BB;
  color: white;
}
.site-van a:active{
  font-weight: bolder;
}
.social{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social .icon-class{
  padding-right: 30px;
  font-size: 25px;
  color: #EBA6BB;
}
.social .icon-class:hover{
  color: #85FFBD;
}
.subtopic{
  margin:10px 10px 30px 10px !important;
}
.widecard{
  width: 100%;
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover{
  color: #1b1d20;
  background-color: #85FFBD;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.widecard:hover .compdet .hidden{
  max-width: fit-content;
  display: block;
  color: #1b1d20;
  background-color: #85FFBD;
}
.widecard .compdet .hidden{
  display: none;
}

.home{
  align-items: center;
  padding-right: 100px;
  padding-top: 100px;
}
.typingeffect{
  text-align: center;
  font-size: x-large;
  font-weight: bold;
}
.profilepic{
  height: 20%;
  width: 40%;
}
.btn {
  outline: none;
  border: none;
  position: relative;
  padding: 15px 25px;
  margin: 10px;
  text-transform: uppercase;
  letter-spacing: 3px;
  border-radius: 25px;
  overflow: hidden;
  transition: all 0.3s ease;
}
.btn:hover {
    cursor: pointer;
    color: #000;
}
.btn::after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease;
}
.btn2 {
  background: #FFFB7D;
  color: #1c1c1c;
}
.btn2:hover {
    background: #EBA6BB;
}
.btn2::after {
    background: #EBA6BB;
    width: 100%;
    height: 100%;
    transform: scale(1);
}
.compdet a{
  padding-left: 400px;
}
p{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
nav{
  padding:60px 30px 0px 30px ;
  flex:var(--sidebar-flex);
  background-color: #85FFBD;
  background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 60%);
  /*background: rgb(238,103,143);
  background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);*/
}

nav ul{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li{
  margin:40px;
  padding: 12px; 
}

